// PREVIOUS CODE OF LOGIC
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { authChangeAction } from "../../../redux/store/authenticate-slice";
import { idChangeAction } from "../../../redux/store/department-slice";
import { financeChangeAction } from "../../../redux/store/finance-slice";
import { admissionChangeAction } from "../../../redux/store/admission-slice";
const useLogin = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const userDispatchAction = (data) => {
    localStorage.clear();
    let user = data?.user;
    if (data?.isUniversal) {
      navigate(`/verify`, {
        state: {
          phoneNumber: data?.phoneNumber,
        },
      });
    } else if (data?.accessFrom === "DELETED_ACCOUNT") {
      dispatch(
        authChangeAction.loginQuery({
          login: true,
          token: data.token,
          id: user?._id,
          status: true,
          loggedinAs: "user",
          username: user?.username,
        })
      );
      navigate(`/account/delete?type=confirm`, {
        state: {
          // accessFrom: data?.accessFrom,
          id: user?._id,
        },
      });
    } else {
      dispatch(
        authChangeAction.loginQuery({
          login: true,
          token: data.token,
          id: user?._id,
          status: true,
          loggedinAs: "user",
          username: user?.username,
        })
      );

      dispatch(
        idChangeAction.addInstituteId({
          id: user?._id,
          loggedinAs: "user",
        })
      );
      navigate(`/q/${user?.username}/feed`);
    }
  };

  const existingAccountLogin = (data, accessFrom) => {
    userDispatchAction({
      token: data?.token,
      user: data?.user,
      accessFrom: accessFrom,
    });
  };
  const instituteDispatchAction = (data) => {
    localStorage.clear();
    let institute = data?.institute;
    if (data?.isUniversal) {
      navigate(`/verify`, {
        state: {
          phoneNumber: data?.phoneNumber,
          u_name: data?.u_name,
        },
      });
    } else {
      dispatch(
        authChangeAction.loginQuery({
          login: true,
          token: data.token,
          id: institute?._id,
          status: true,
          loggedinAs: "institute",
          username: institute?.name,
          loginRole:
            data?.main_role === "SOCIAL_MEDIA_HANDLER" ? "_usr_rs" : "",
        })
      );

      dispatch(
        idChangeAction.addInstituteId({
          id: institute?._id,
          loggedinAs: "institute",
        })
      );
      dispatch(
        financeChangeAction.financeQuery({
          fid: institute?.financeDepart[0] ? institute?.financeDepart[0] : "",
        })
      );
      dispatch(
        admissionChangeAction.admissionQuery({
          aid: institute?.admissionDepart[0]
            ? institute?.admissionDepart[0]
            : "",
        })
      );
      navigate(`/q/${institute?.name}/feed`, {
        state: {
          loginRole:
            data?.main_role === "SOCIAL_MEDIA_HANDLER" ? "_usr_rs" : "",
        },
      });
    }
  };
  return { userDispatchAction, existingAccountLogin, instituteDispatchAction };
};

export { useLogin };
